
import { mapState, mapActions, mapMutations } from 'vuex'
import { local } from './../utils/storage'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { Modules, request} from '../utils/HttpRequest/request';
import { goDetails} from '../utils/hexiao';
import { couponStatus } from './../config/constant'
import Wechat from './../utils/wechat'

export default {
components: {
Tab,
TabItem,
Sticky,
Divider,
XButton,
XHeader,
LoadMore
},
data() {
return {
couponStatus,
amount_type: 1,
selectedStatus: couponStatus.UNUSED,
couponList: [
{text: '未使用', value: couponStatus.UNUSED },
{text: '已使用', value: couponStatus.USED },
{text: '已过期', value: couponStatus.EXPIRED }
],
tabIndex: 1,
couponShow: 0,
phone: '',
page: 1,
perpage: 100,
tip: '正在加载',
no_data: '暂无数据',
loading: false,
coupon_code:'',
}
},
methods: {
...mapActions({
getMyCoupons: 'getMyCoupons'
}),

handler(status) {
console.log('choising: ', status)
this.selectedStatus = status
this.getCoupons(status);
},

goDetail(idx) {
goDetails(idx, 1, this.$router)
local.save(local.KEY_TEMP_COUPON_INFO, idx)
},

usedDetails(idx) {
goDetails(idx, 1, this.$router)
local.save(local.KEY_TEMP_COUPON_INFO, idx)
},

overDueDetails(idx) {
goDetails(idx, 1, this.$router)
local.save(local.KEY_TEMP_COUPON_INFO, idx)
},

getCoupons(status, page) {
this.notUsedList = []
this.usedList = []
this.overDueList = []
this.loading = true
console.log('STATUS ', status)
const bundle = page ? { status: status || this.selectedStatus, page } : { status }
this.getMyCoupons(bundle).then(res => {
this.loading = false
})
},

getNextCoupons() {
this.loading = true
console.log('STATUS NEXT', this.selectedStatus)
const bundle = { status: this.selectedStatus }
const scroller = this.$refs['scroller_' + this.selectedStatus]
this.getMyCoupons(bundle).then(res => {
this.loading = false
scroller.finishPullToRefresh()
scroller.finishInfinite(true)
})
},
},
created () {
Wechat.initHomeShare()
},
mounted() {
this.getCoupons(couponStatus.UNUSED, 1)
},
computed: {
...mapState({
myCouponsData: state => state.coupon.couponsData
})
}
}
